.module-dialog-layer {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.8;
  filter: alpha(opacity=80);
}
.module-dialog {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1001;
  text-align: center;
  opacity: 0;
  transition: opacity 0.2s ease-in;
}
.module-dialog:after {
  content: '';
  height: 100%;
  margin-left: -0.25em;
}
.module-dialog .dialog-panel,
.module-dialog:after {
  display: inline-block;
  vertical-align: middle;
}
.module-dialog .dialog-panel {
  position: relative;
  width: 436px;
  min-width: 200px;
  background: #fff;
  border-radius: 8px;
  border: 1px solid #dcdcdc;
  border-color: rgba(0, 0, 0, 0.14);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 1);
  text-align: left;
}
.module-dialog .topbar {
  overflow: hidden;
  width: 100%;
  height: 69px;
  border-radius: 13px 13px 0 0;
}
.module-dialog .dialog-tit {
  height: 60px;
  padding: 0 15px;
  border-radius: 8px 8px 0 0;
  border-bottom: 1px solid #d4d4d4;
  box-shadow: rgba(0, 0, 0, 0.06) 0 1px 7px;
  background: linear-gradient(#fbfbfb, #ececec);
  line-height: 60px;
}
.module-dialog .dialog-tit h4 {
  float: left;
  padding-left: 15px;
  font-size: 18px;
  font-weight: 400;
  color: #333;
}
.module-dialog .dialog-close {
  overflow: hidden;
  display: block;
  position: absolute;
  right: 17px;
  top: 18px;
  z-index: 20;
  width: 16px;
  height: 17px;
  padding: 4px 5px;
  background: url(/react-demo/static/media/ui.00b79aea.png) no-repeat -16px -16px;
  text-indent: -9999em;
  opacity: 0.7;
  cursor: pointer;
  transition: all 0.3s linear;
}
.module-dialog .dialog-close:hover {
  opacity: 1;
}
.module-dialog .dialog-con {
  min-width: 220px;
}
.module-dialog .confirm-error-msg,
.module-dialog .confirm-msg {
  max-width: 310px;
  min-height: 50px;
  margin: 50px auto;
  line-height: 24px;
  text-align: center;
  word-wrap: break-word;
  word-break: break-all;
  font-size: 16px;
}
.module-dialog .dialog-btn-wrap {
  height: 51px;
  padding: 18px 10px 0;
  line-height: 51px;
  border-radius: 0 0 13px 13px;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAABFCAYAAACL3IzzAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAADpJREFUeNpi+P//PwMIMDEBCWYQwYJKsIIIXoYfP378ZwIrRBAgvehiRMiSJEGEGJnaqOhILLIAAQYAPiIuZT5QGrcAAAAASUVORK5CYII=) 0 0 repeat-x;
}
.blue-main-btn,
.gray-main-btn,
.jianguo-blue-main-btn {
  display: block;
  padding: 1px;
  margin: 0 auto;
  border-radius: 9px;
  background: #015e94;
  background: linear-gradient(#5598c9, #2a6da2);
  text-align: center;
  text-shadow: rgba(255, 255, 255, 0.496094) 0 1px 0;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.normal-main-btn {
  height: 38px;
}
.module-dialog .dialog-btn-wrap .blue-main-btn {
  float: right;
}
.module-dialog .dialog-btn-wrap .blue-main-btn,
.module-dialog .dialog-btn-wrap .blue-main-btn a {
  width: 100px;
}
.blue-main-btn a,
.gray-main-btn a,
.jianguo-blue-main-btn a {
  display: block;
  padding: 2px 0;
  border-radius: 9px;
  background: #5f7ed7;
  background: linear-gradient(#739fe1, #5f7ed7);
  box-shadow: inset 0 1px 3px #92b6ec, inset 0 0 2px #627dca,
    inset 0 -2px 3px #4c69b8;
  text-shadow: #4f70b3 0 -1px 0;
  cursor: pointer;
  color: #fff;
  transition: all 0.3s ease;
}
.normal-main-btn a {
  height: 34px;
  line-height: 34px;
}
.blue-main-btn a:hover {
  box-shadow: inset 0 1px 3px #83a9e1, inset 0 0 2px #627dca,
    inset 0 -2px 3px #5a77c7, inset 0 0 100px rgba(48, 77, 147, 0.4);
}
.blue-main-btn a:active {
  box-shadow: inset 0 2px 5px #3d67a5, inset 0 0 2px #627dca,
    inset 0 0 100px rgba(25, 108, 232, 0.5);
}
.item .item-box {
  width: 1098px;
  padding: 60px;
  margin-bottom: 20px;
  display: table;
  overflow: hidden;
  background: #fff;
  border-radius: 8px;
  border: 1px solid #dcdcdc;
  border-color: rgba(0, 0, 0, 0.14);
  box-shadow: 0 3px 8px -6px rgba(0, 0, 0, 0.1);
}
.banner,
.gallery-wrapper {
  display: table-cell;
}
.gallery-wrapper {
  vertical-align: top;
}
.gallery {
  float: left;
  width: 540px;
  display: table-cell;
}
.thumb,
.thumbnail {
  display: table-cell;
  vertical-align: middle;
}
.thumbnail li {
  width: 54px;
  height: 54px;
  margin-top: 10px;
  padding: 12px;
  border: 1px solid rgba(0, 0, 0, 0.06);
  border-radius: 5px;
  cursor: pointer;
}
.thumbnail li:first-child {
  margin-top: 0;
}
.thumbnail li.on {
  padding: 10px;
  border: 3px solid #ccc;
  border: 3px solid rgba(0, 0, 0, 0.2);
}
item-box img {
  max-width: 100%;
  height: auto;
}
.thumb > ul {
  margin-left: 20px;
  width: 440px;
  height: 440px;
  position: relative;
}
.thumb li {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: none;
  opacity: 0;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.thumb li.on {
  -webkit-animation: thumb-change 0.35s ease-out 1;
          animation: thumb-change 0.35s ease-out 1;
  position: relative;
  display: block;
  z-index: 1;
  opacity: 1;
}
.item-box .banner {
  vertical-align: middle;
  width: 450px;
  margin-left: 10px;
}
.item-box .banner .sku-custom-title {
  overflow: hidden;
  padding: 8px 8px 18px 10px;
  position: relative;
}
.item-box .banner .params-price {
  position: absolute;
  right: 8px;
  bottom: 19px;
}
.item-box .banner .params-price span {
  display: block;
  color: #de4037;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-align: right;
}
.item-box .banner .params-price span i {
  padding-left: 2px;
  font-size: 24px;
}
.item-box .banner .sku-custom-title .params-info {
  width: 360px;
}
.item-box .banner .sku-custom-title h4 {
  font-size: 24px;
  line-height: 1.25;
  color: #000;
  margin-bottom: 13px;
}
.item-box .banner .sku-custom-title h6 {
  font-size: 14px;
  line-height: 1.5;
  color: #bdbdbd;
}
.item-box .banner .sku-dynamic-params-panel {
  padding: 29px 0 8px 10px;
  border-top: 1px solid #ebebeb;
}
.item-box .banner .sku-dynamic-params {
  margin-bottom: 19px;
}
.item-box .banner .params-name {
  float: left;
  padding-right: 20px;
  font-size: 14px;
  color: #8d8d8d;
  line-height: 36px;
}
.item-box .banner .sku-dynamic-params .params-colors {
  float: left;
  line-height: 36px;
  margin: -10px 0 0 -10px;
  width: 402px;
}
.item-box .banner .sku-dynamic-params .params-colors > li {
  float: left;
  margin: 10px 0 0 10px;
}
.item-box .banner .sku-dynamic-params .params-colors > li a {
  display: block;
  width: 26px;
  height: 26px;
  border: 2px solid #e5e5e5;
  padding: 3px;
  text-align: center;
  color: #757575;
  border-radius: 50%;
}
.item-box .banner .sku-dynamic-params .params-colors > li.cur a {
  border-color: #b2b2b2;
  box-shadow: inset 0 0 0 1px #b2b2b2;
}
.item-box .banner .sku-dynamic-params .params-colors > li i,
.item-box .banner .sku-dynamic-params .params-colors > li img {
  position: relative;
  display: block;
  width: 100%;
  border-radius: 50%;
}
.item-box .banner .sku-dynamic-params {
  margin-bottom: 19px;
}
.item-box .banner .params-detail {
  line-height: 36px;
  float: left;
  width: 392px;
}
.item-box .item-num {
  float: left;
  width: 128px;
}
.item-box .item-num .down:before,
.item-box .item-num .up:before {
  content: '';
  position: absolute;
  left: -4px;
  right: -4px;
  top: 0;
  height: 45px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: 100% auto;
}
.item-box .item-num .down:before {
  content: ' ';
  background-position: 0 -60px;
}
.item-box .item-num .down:hover:before {
  content: ' ';
  background-position: 0 -180px;
}
.item-box .item-num .down.down-disabled:before,
.item-box .item-num .down.down-disabled:hover:before {
  content: ' ';
  background-position: 0 -300px;
}
.item-box .item-num .down,
.item-box .item-num .up {
  position: relative;
  float: left;
  /* display: inline-block; */
  width: 36px;
  height: 36px;
  line-height: 40px;
  text-indent: -9999em;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.item-box .item-num .down.down-disabled,
.item-box .item-num .up.up-disabled {
  cursor: not-allowed;
}
.item-box .item-num .up:before {
  background-position: 0 0;
}
.item-box .item-num .up:hover:before {
  background-position: 0 -120px;
}
.item-box .item-num .up.up-disabled:before,
.item-box .item-num .up.up-disabled:hover:before {
  content: ' ';
  background-position: 0 -240px;
}
.item-box .item-num .num {
  position: relative;
  overflow: hidden;
  float: left;
  /* display: inline-block; */
  width: 56px;
  height: 18px;
  margin: 7px 0 0;
  border: none;
  border-radius: 3px;
  line-height: 18px;
  text-align: center;
  font-size: 14px;
}
.item-box .item-num input {
  width: 56px;
  height: 18px;
  background-color: transparent;
  border: none;
  border-radius: 3px;
  text-align: center;
  font-size: 14px;
  line-height: 18px;
  padding: 0;
}
.item-box .item-num ul {
  /*display: none;*/
  position: absolute;
  left: 0;
  top: 0;
  width: 56px;
}
.item-box .item-num ul li {
  width: 56px;
  height: 18px;
}
.item-box .sku-status {
  position: relative;
  border-top: 1px solid #ebebeb;
  padding: 30px 0 0 10px;
}
.item-box .blue-title-btn {
  float: left;
  /* display: inline-block; */
  width: 143px;
  height: 48px;
  line-height: 48px;
  text-align: center;
  border-radius: 7px;
  border: 1px solid #5c81e3;
  background-color: #5c85e5;
  background-image: linear-gradient(#779ae9, #5078df);
  color: #fff;
  cursor: pointer;
}
.item-box .blue-title-btn:hover {
  transition: all 0.15s ease-out;
  box-shadow: inset 0 1px 1px #7696de, inset 0 0 2px #627dca,
    inset 0 -2px 3px #5a77c7, inset 0 0 100px rgba(48, 77, 147, 0.4);
}
.item-box .blue-title-btn a,
.item-box .green-title-btn a {
  color: #fff;
}
.item-box .cart-operation-wrapper .gray-title-btn {
  margin-left: 20px;
}
.item-box .gray-title-btn {
  display: inline-block;
  width: 143px;
  height: 48px;
  line-height: 48px;
  text-align: center;
  background: linear-gradient(#fff, #fafafa);
  border: 1px solid #e0e0e0;
  border-radius: 7px;
  color: #8c8c8c;
  cursor: pointer;
}
.item-box .gray-title-btn:hover {
  transition: all 0.15s ease-out;
  background: linear-gradient(#f6f6f6, #ededed);
}

